import { useState } from "react";
import { useTranslation } from "react-i18next";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import ModalMenu from "../../../Ui/Modal/ModalMenu";

const ClassCatalogResuffleMenu = ({ onClose, onSaveAction }) => {
  const { t } = useTranslation();
  const [state, setState] = useState("");

  const onAction = (val) => {
    setState(val);
    onSaveAction(val);
    onClose();
  };

  return (
    <ModalMenu
      onClose={onClose}
      customStyle={{
        width: "28rem",
      }}
    >
      <h6>{t("alphabetical")}</h6>
      <div>
        <TileWrapper
          customTileContainer={{
            marginBottom: "0.7rem",
          }}
        >
          {admissionIntakeType?.map((tile) => (
            <TileCard
              key={tile?.key}
              tile={tile}
              onTile={onAction}
              activeTile={state}
            />
          ))}
        </TileWrapper>
      </div>
    </ModalMenu>
  );
};

export default ClassCatalogResuffleMenu;

export const admissionIntakeType = [
  {
    key: "Alpha",
    name: "First Name",
  },
  {
    key: "Alpha_Last",
    name: "Surname",
  },
  {
    key: "Gender_Alpha",
    name: "Gender and Alphabetical",
  },
];
